body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
}
body {
  width: 100%;
  height: 100%;
  background-attachment: fixed;
}

.app {
  text-align: center;
  height: 100%;
  background-color: #f8f9fa;
}

.app-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #f8f9fa;
}

/* General APP styles */

.sub-title {
  color: #4a4f55;
}

.sub-sub-title {
  color: #919ba7;
}

.app-header {
  background-color: white !important;
  box-shadow: 0 0.5rem 1.2rem #bdc5d133;
}

.app-card {
  box-shadow: 0 0.2rem 1.2rem #bdc5d133;
}

/* Small details / layout */

canvas {
  border: 1px solid #dfdfdf !important;
  /* box-shadow: 0 0.2rem 1.2rem #bdc5d133; */
}

.side-column-controls {
  max-height: 800px;
  overflow-y: scroll;
}

.horizontal-line {
  padding: 0.4rem 0;
  margin: 1rem 0;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.correct-container-padding {
  padding: 0px 15px !important;
}
@media (max-width: 576px) {
  .correct-container-padding {
    padding: 0px 0px;
  }
}

/* Edits bootstrap default */

.card-body {
  padding: 1rem 1.25rem !important;
}
.won {
  background: #f2ffd8; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(
    left,
    #f2ffd8 0%,
    #ffffff 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to right,
    #f2ffd8 0%,
    #ffffff 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2ffd8', endColorstr='#ffffff',GradientType=1 ); /* IE6-9 */
}

/* Animations */

.pending {
  animation: pulse-opacity 1s infinite ease-in-out;
  -webkit-animation: pulse-opacity 1s infinite ease-in-out;
  --opacity-from: 0.2;
  --opacity-to: 1;
}

@keyframes pulse-opacity {
  0% {
    opacity: var(--opacity-from);
  }
  50% {
    opacity: var(--opacity-to);
  }
  100% {
    opacity: var(--opacity-from);
  }
}
@-webkit-keyframes pulse-opacity {
  0% {
    opacity: var(--opacity-from);
  }
  50% {
    opacity: var(--opacity-to);
  }
  100% {
    opacity: var(--opacity-from);
  }
}

